import React from 'react'
import Logo from '../Images/ScamCheckLogoTick.svg'

class Header extends React.Component {
  render() {
    return(
      <header className="HeaderCon">
          <div className="Header">
            <img src={Logo} alt="Logo"></img>
            <div className="HeaderTitle">ScamCheck</div>
            <div className="HeaderItem">Services</div>
            <div className="HeaderItem">About Us</div>
          </div>
          
      </header>
    )
  }
}

export default Header;