import React from 'react'
import LandingImage from '../LandingImage.png'
import LandingVector from '../Scam Landing Page_updated.svg'
import CardDocument from '../Images/CardDocument.svg'
import CardBrainNodes from '../Images/CardBrainNodes.svg'
import CardWebsite from '../Images/CardWebsite.svg'

class Landing extends React.Component {
  render() {
    return(
      <React.Fragment>
      <main className="Landing">
          
            <div className="LandingMediaCon">
              <div className="LandingText">
                <div className="LandingTextCentre">
                  <h1>
                    Instantly Verify<br></br>the Legitimacy<br></br>of any Website
                  </h1>
                  <h2>
                    Suspicious link?<br></br>We can tell you the trustworthiness of that site within a matter of seconds. 
                  </h2>

                </div>
              </div>
              <div className="LandingImgCon">
                <img className="LandingImg" src={LandingVector} alt="Landing Page Art"></img>
              </div>
              <form className="SearchBarCon">
                  <input type="text" className="SearchBar" placeholder="Place URL Here..."></input>
              </form>
            </div>
          
      </main>
      <div className="TitleContentCon">
        <div className="TitleContent">
          <div className="TitleContentTile">
            <img className="TileImg" src={CardDocument} />
            <div className="TileTitle">Lorem ipsum</div>
            <div className="TileText">Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua.</div>
          </div>
          <div className="TitleContentTile">
            <img className="TileImg" src={CardBrainNodes} />
            <div className="TileTitle">Lorem ipsum</div>
            <div className="TileText">Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua.</div>
          </div>
          <div className="TitleContentTile">
            <img className="TileImg" src={CardWebsite} />
            <div className="TileTitle">Lorem ipsum</div>
            <div className="TileText">Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua.</div>
          </div>
        </div>
      </div>
      </React.Fragment>
    )
  }
}

export default Landing;